import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { StockTransferPage } from '../stock-transfer/stock-transfer';
import { LoginPage } from '../login/login';
import { CommonProvider } from '../../providers/common/common';
import { PickListPage } from '../pick-list/pick-list'; 
import { PickListSearchItemPage } from '../pick-list-search-item/pick-list-search-item'; 
import { GoodsReceiptPage } from '../goods-receipt/goods-receipt'; 
import { GoodsReceiptProvider} from '../../providers/goods-receipt/goods-receipt'  ;
import { ImportPage } from '../import/import';
import { SalesInvoicePage } from '../salesinvoice/salesinvoice';   
import { SalesInvoiceProvider } from '../../providers/sales-invoice/sales-invoice';
import { RouteTrackingPage } from '../rotue-tracking/rotue-tracking'; 
import { DayOpeningPage } from '../dayopening/dayopening'; 
import { SalesInvoiceItemsPage } from '../salesinvoice-items/salesinvoice-items';
import { ReportsPage } from '../reports/reports';
import { IncomingPage } from '../incoming/incoming';  
import { DayclosingPage } from '../dayclosing/dayclosing'; 
import { IncomingProvider } from '../../providers/incoming/incoming';
import { RoutePlanPage } from '../route-plan/route-plan';
import { RouteVisitEntryPage } from '../route-visit-entry/route-visit-entry';
import { UserProfilePage } from '../user-profile/user-profile';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  constructor(public navCtrl: NavController,
    public common: CommonProvider,
    public GoodReceiptProvider:GoodsReceiptProvider,
    public SalesInvoiceProvider:SalesInvoiceProvider,
    public IncomingProvider :IncomingProvider
    ) {
  
  }

  StockTransfer()
  {
    this.navCtrl.push(StockTransferPage)
  }
  logout(){
    this.common.companyName="";
    this.common.password="";
    this.common.userId="";
    this.navCtrl.setRoot(LoginPage);
  }
  PickList()
  {
    this.navCtrl.push(PickListPage)
  }
  GoodsReceipt()
  {
    this.GoodReceiptProvider.venderCode="";
    this.navCtrl.push(GoodsReceiptPage)
  } 
  ReportsList()
  {
    this.navCtrl.push(ReportsPage) 
  }
  IncomingPayment()
  {
    this.IncomingProvider.clearAllProperties();
    this.navCtrl.push(IncomingPage) 
  } 
  ItemSearch()
  {
    this.navCtrl.push(PickListSearchItemPage) 
  }  
  ImportProduct()
  {
    this.navCtrl.push(ImportPage) 
  }  
  RouteSearch()
  {
    this.navCtrl.push(RouteTrackingPage) 
  }   
  RoutePlan()
  {
     this.navCtrl.push(RoutePlanPage) 
  }   
  RouteVisit()
  {
     this.navCtrl.push(RouteVisitEntryPage)
  }
  SalesInvoice()
  {  
    this.SalesInvoiceProvider.clearAllProperties(); 
    this.navCtrl.push(SalesInvoicePage)
  } 
  DayOpening()
  {
    this.navCtrl.push(DayOpeningPage) 
  }  
  DayClosing()
  {
    this.navCtrl.push(DayclosingPage) 
  }  
  UserP()
  {
    this.navCtrl.push(UserProfilePage)
  }
}



