  createTable() {
    this.database.databaseObj.executeSql(`CREATE TABLE IF NOT EXISTS USER (ucd varchar(20), unm varchar(100), 
          psw varchar(50), devid varchar(100), whscd varchar(20), slpcd varchar(20), slpnm varchar(100), 
          brncd varchar(20), brnnm varchar(100), series varchar(20), nextnum int, currency varchar(10),
          pricelist varchar(50), superusr varchar(1))`, [])
      .then(() => {
       //alert('Table Created!');
      }) 
      .catch(e => {
        alert("error " + JSON.stringify(e))
      });      
  }

  insertRow() { 
    this.database.databaseObj.executeSql(`INSERT INTO USER (ucd,unm,psw,devid,whscd,slpcd,slpnm,brncd,brnnm,series,currency) 
     VALUES ('wms','Piyush Vadukiya','1234','1234567890','01','','','','','','');`, [])
      .then(() => {
       //alert('Row Inserted!'); 
      })
      .catch(e => {
        alert("error " + JSON.stringify(e))
      });
  }

  getRows() { 
    this.database.databaseObj.executeSql(`SELECT * FROM USER`, [])
      .then((res) => {
        this.row_data = [];
        if (res.rows.length > 0) {
          for (var i = 0; i < res.rows.length; i++) {
            this.row_data.push(res.rows.item(i));
          }
        } 
      })
      .catch(e => {
        alert("error " + JSON.stringify(e))
      });
  }

  GetUser() { 
    // let loading = this.loadingCtrl.create({
    //   content: 'Please wait...'
    // });  
    // loading.present(); // Loding Start
  
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.masterdata +'/userlist?pageIndex=100')
    .subscribe((response:any) => {
      // loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){
        
        var i=0 ;
        var query = "";
        query += " INSERT INTO USER (ucd,unm,psw,devid,whscd,slpcd,slpnm,brncd,brnnm,series,nextnum,currency,pricelist,superusr) ";
        query += " VALUES ";
       
        for(var i=0; i<= response.json().MessageBody.length-1; i++)
        {  
          if(i!=0) { query +=","; }
           //alert(response.json().MessageBody[i].UserCode);
          query += " ( '"+ response.json().MessageBody[i].UserCode +"' ,     ";
          query += "   '"+ response.json().MessageBody[i].UserName +"' ,     ";
          query += "   '"+ response.json().MessageBody[i].Password +"'  ,    ";
          query += "   '"+ response.json().MessageBody[i].DeviceId +"',      ";
          query += "   '"+ response.json().MessageBody[i].Warehouse +"',     ";
          query += "   '"+ response.json().MessageBody[i].SalesManCode +"',  ";
          query += "   '"+ response.json().MessageBody[i].SalesManName +"',  ";
          query += "   '"+ response.json().MessageBody[i].BranchCode +"',    ";
          query += "   '"+ response.json().MessageBody[i].BranchName +"',    ";
          query += "   '"+ response.json().MessageBody[i].Series +"',        ";
          query += "   '"+ response.json().MessageBody[i].NextNumber +"',    ";
          query += "   '"+ response.json().MessageBody[i].Currency +"',      ";
          query += "   '"+ response.json().MessageBody[i].PriceList +"',     ";
          query += "   '"+ response.json().MessageBody[i].SuperUser +"' )    ";  
          
          //alert(response.json().MessageBody[i].PriceList + " - "+ response.json().MessageBody[i].SuperUser  + " - "+ response.json().MessageBody[i].DeviceId)
        }
      
        query += ";"

        this.database.databaseObj.executeSql("DELETE FROM USER", [])
        .then(() => {
              this.database.databaseObj.executeSql(query, [])
              .then(() => { 
                  alert("Device ID : "+this.common.deviceuuid); 
              })
              .catch(e => {
                alert("error "+ query + " " + JSON.stringify(e))
              });   
        })
        .catch(e => {
          alert("error "+ query + " " + JSON.stringify(e))
        });   
      }
    }, error => {
      //alert("Something went wrong!!!");
      alert("Something went wrong!!!"+JSON.stringify(error));
      // loading.dismiss(); // Loding Dismiss
    });    
  }
