import { Component } from '@angular/core'; 
import { IonicPage, NavController, NavParams,LoadingController,AlertController } from 'ionic-angular';
import { Http } from '@angular/http'; 
import { CommonProvider } from '../../providers/common/common'; 
import { SQLite, SQLiteObject } from  '@ionic-native/sqlite';  
import { DatabaseProvider } from '../../providers/database/databaseprovider';
import { SalesInvoiceProvider} from '../../providers/sales-invoice/sales-invoice';
import { IncomingProvider } from '../../providers/incoming/incoming';

/**
 * Generated class for the CustomerSearchPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-customer-search',
  templateUrl: 'customer-search.html',
})
export class CustomerSearchPage {

  listItem:any=[]   
  CustCodeOrName:any=""

  constructor(public navCtrl: NavController, public navParams: NavParams,
    public http:Http,public alertCtrl:AlertController,
    public loadingCtrl:LoadingController,
    public common: CommonProvider,
    public SalesInvoiceProvider:SalesInvoiceProvider,
    public IncomingProvider:IncomingProvider,
    private sqlite: SQLite, 
    private database:DatabaseProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CustomerSearchPage');
  }
 
  getOfflineItems() { 
    var qry = "";
    qry ="SELECT code,name FROM MASTER WHERE type='2' ";
    qry +=  "  and userid='"+this.common.userId+"' and (code like '%"+this.CustCodeOrName+"%' ";
    qry +=  "  or name like '%"+this.CustCodeOrName+"%')";
    qry +=  " order by name ";

    this.database.databaseObj.executeSql(qry, [])
      .then((res) => {
        
        this.listItem = [];
        if (res.rows.length > 0) { 
          for (var i = 0; i < res.rows.length; i++) {
              this.listItem.push(res.rows.item(i));
              
            }
        } 
      })
      .catch(e => {
        alert("error " + JSON.stringify(e))
      });
  }

  returncode(Code,Name)
  {
    this.SalesInvoiceProvider.custCode=Code;
    this.SalesInvoiceProvider.custName=Name;
    this.IncomingProvider.custCode=Code;
    this.IncomingProvider.custName=Name;
    this.navCtrl.pop();
  }
 

}



