        query += "                     qty      , price    ,  discrt   , discamt  ,  amt      , taxcd    , taxrate  , taxsum,  slpcd    , slpnm    , addr     ,  ";
        query += "                     addr2    , mobile   ,  manage   , uniqueid  ,  userid   , remark   , freetxt, chqno )";
        query += " VALUES   " ;
     
