import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,LoadingController,AlertController } from 'ionic-angular';
import { Http } from '@angular/http'; 
import { CommonProvider } from '../../providers/common/common'; 
import { SQLite, SQLiteObject } from  '@ionic-native/sqlite';  
import { DatabaseProvider } from '../../providers/database/databaseprovider';
import { SalesInvoiceProvider} from '../../providers/sales-invoice/sales-invoice';
import { StockTransferProvider } from '../../providers/stock-transfer/stock-transfer';

/**
 * Generated class for the ItemSearchPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-item-search',
  templateUrl: 'item-search.html',
})
export class ItemSearchPage {

  listItem:any=[]   
  CustCodeOrName:any=""

  constructor(public navCtrl: NavController, public navParams: NavParams,
    public http:Http,public alertCtrl:AlertController,
    public loadingCtrl:LoadingController,
    public common: CommonProvider,
    public SalesInvoiceProvider:SalesInvoiceProvider,
    public StockTransferProvider:StockTransferProvider,
    private sqlite: SQLite, 
    private database:DatabaseProvider)  {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ItemSearchPage');
  }

  getOfflineItems() { 
    var qry = "";
    qry = " SELECT distinct code,name,qty-ifnull((select sum(qty) from TRANS WHERE docstatus!='SAP' ";
    qry += " and TRANS.itmcd=MASTER.code and TRANS.uniqueid=MASTER.uniqueid),0) qty FROM MASTER WHERE type='4' and  ";
    qry += " userid='"+this.common.userId+"' and (code like '%"+this.CustCodeOrName+"%' or name like '%"+this.CustCodeOrName+"%' ) ";
    qry += " order by name ";
    qry += " ";
    
    this.database.databaseObj.executeSql(qry, [])
      .then((res) => {
        this.listItem = [];
        if (res.rows.length > 0) { 
          for (var i = 0; i < res.rows.length; i++) {
             if(res.rows.item(i).qty>0)
              this.listItem.push(res.rows.item(i));
            }
        } 
      })
      .catch(e => {
        alert("error " + JSON.stringify(e))
      });
  }

  returncode(Code,Name,Quantity)
  {
    this.SalesInvoiceProvider.itemCode=Code;
    this.SalesInvoiceProvider.itemName=Name;
    this.SalesInvoiceProvider.quantity=Quantity;
    this.StockTransferProvider.itemcd=Code;
    this.StockTransferProvider.itemnm=Name;
    this.navCtrl.pop();
  }
 
}



