import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { IncomingProvider } from '../../providers/incoming/incoming'; 
import { SQLite, SQLiteObject } from  '@ionic-native/sqlite';  
import { DatabaseProvider } from '../../providers/database/databaseprovider';

/**
 * Generated class for the SearchinvoicePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-searchinvoice',
  templateUrl: 'searchinvoice.html',
})
export class SearchinvoicePage {

  listItem:any=[]   
  CustCodeOrName:any=""

  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    public IncomingProvider:IncomingProvider,
    private sqlite: SQLite, 
    private database:DatabaseProvider) { 
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SearchinvoicePage');
  }

  getOfflineItems() {  
    var query = "";
    query = " SELECT DISTINCT docentry, docnum, replace(docdt,' 00:00:00', '') docdt, round(amt,3) amt, uniqueid ";
    query += " FROM TRANS ";
    query += " WHERE docstatus='SAP' and objtype='13' and docnum like '%"+this.CustCodeOrName+"%' ";
    query += "     and bpcode='"+ this.IncomingProvider.custCode  +"'   ";
    query += "     and docentry not in (Select baseentry  recamt from TRANS    "; 
    query += "         where docstatus!='SAP' and bpcode='"+ this.IncomingProvider.custCode  +"'  )  "; 
    query += " order by docnum  ";
    query += "   ; "; 
    query += "    "; 

    this.database.databaseObj.executeSql(query, [])
      .then((res) => {
        this.listItem = [];
        if (res.rows.length > 0) {  

          for (var i = 0; i < res.rows.length; i++) {
             if(res.rows.item(i).amt>0)
              this.listItem.push(res.rows.item(i));
            }
        } 
      })
      .catch(e => {
        alert("error " + JSON.stringify(e))
      });
  }

  returncode(Code,Name,DocTotal,BalanceAmt,BaseEntry)
  {
    this.IncomingProvider.basenum=Code;
    this.IncomingProvider.basedt=Name;
    this.IncomingProvider.baseamt=DocTotal;
    this.IncomingProvider.basebalamt=BalanceAmt;
    this.IncomingProvider.baseentry=BaseEntry;
    this.navCtrl.pop();
  }
 

}



