import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,LoadingController,AlertController } from 'ionic-angular';
import { Http } from '@angular/http';
import { AppConstant } from '../../app/app.constant';
import { PickListProvider } from '../../providers/pick-list/pick-list';


@IonicPage()
@Component({
  selector: 'page-pick-list-search-vender',
  templateUrl: 'pick-list-search-vender.html',
})
export class PickListSearchVenderPage {
  customerList:any[]
  pageIndex:any=""
  customerCodeOrName:any=""
  constructor(public navCtrl: NavController, public navParams: NavParams, public http:Http,public alertCtrl:AlertController,
    public loadingCtrl:LoadingController,public PickListProvider:PickListProvider) {
     
  }

  GetCustomerList()
  {
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.masterdata +'/customers'+'?pageIndex='+'1'+'&all='+this.customerCodeOrName)
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){
       this.customerList=response.json().MessageBody;
      }
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }
  returncode(Code,Name)
  {
    this.PickListProvider.VenderName=Name;
    this.PickListProvider.VenderCode=Code;
    this.navCtrl.pop();
  }
}



