import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,LoadingController,AlertController } from 'ionic-angular';
import { AppConstant } from '../../app/app.constant';
import { CommonProvider } from '../../providers/common/common'; 
import { Http,RequestOptions,Headers } from '@angular/http';  
import { IncomingProvider } from '../../providers/incoming/incoming'; 
import { IncomingViewPage } from '../incoming-view/incoming-view';
import { SearchinvoicePage } from '../../pages/searchinvoice/searchinvoice';
import { SQLite, SQLiteObject } from  '@ionic-native/sqlite';   
import { DatabaseProvider } from '../../providers/database/databaseprovider';

@IonicPage()
@Component({
  selector: 'page-incoming-entry',
  templateUrl: 'incoming-entry.html',
})

export class IncomingEntryPage { 
  
  today:any = new Date().toISOString();
  docDate:any=new Date().toISOString().split("T")[0];
  postDate:any=new Date().toISOString().split("T")[0];
  inventry:any="";
  invno:any="";
  invdt:any="";
  invamt:any="";
  opnamt:any="";
  recamt:any="";
  warehouseList:any=[];
  fromLocation:any="";
  toLocation:any="";
  refDoc:any="";
  refDocNo:any="";
  VenderName:any="";
  poList:any=[];
  icList:any=[];
  poNo:any="";
  icNo:any="";
  headerRetrieveObject: any={};
  isRetrieve=false;

  constructor(public navCtrl: NavController, public navParams: NavParams,
    public http:Http,public alertCtrl:AlertController,
    public IncomingProvider:IncomingProvider,
    public loadingCtrl:LoadingController,
    public common: CommonProvider,
    private sqlite: SQLite, 
    private database:DatabaseProvider) {
  
  }

  searchItem()
  {
    this.navCtrl.push(SearchinvoicePage);
  }

  
  ionViewDidEnter(){
    if(this.IncomingProvider.basenum!=""){  
      this.inventry=this.IncomingProvider.baseentry;
      this.invno=this.IncomingProvider.basenum;
      this.invdt=this.IncomingProvider.basedt;
      this.invamt=this.IncomingProvider.baseamt;
      this.opnamt=this.IncomingProvider.basebalamt;
    }
  }
  
  
  savetemp()
  { 
    if(this.recamt==0)
    {
      let alert = this.alertCtrl.create({
        title: "Error",
        message: "Please fill all the fields",
        buttons: ['OK']
      });
      alert.present();
    }
    else
    {   
        let loading = this.loadingCtrl.create({
          content: 'Please wait...'
        });   
        loading.present(); // Loding Start
    
        var query = ""; 
        query += "INSERT INTO TRANS (  docstatus, brncd, docentry ,  docnum   , objtype  , bpcode   ,  bpname   , linenum  , docdt    , baseentry, basetype ,   ";
        query += "                     baseline , visorder ,  basenum  ,  blocknum , numatcard, currency ,  itmcd    , itmnm    , barcode  , uom      , whscd    ,   ";
        query += "                     qty      , price    ,  discrt   , discamt  ,  amt      , taxcd    , taxrate  , taxsum,  slpcd    , slpnm    , addr     ,  ";
        query += "                     addr2    , mobile   ,  manage   , uniqueid  ,  userid   , remark   , freetxt,  chqno, chqdt, chqduedt, chqamt )";
        query += " VALUES   " ;

             query += " ('TEMP',    "; 
             query += "'"+ this.common.brncd + "', ";  
             query += "'"+ this.IncomingProvider.docentry + "', ";  
             query += "'"+ this.IncomingProvider.docNo + "', ";  
             query += "'24',";  
             query += "'"+ this.IncomingProvider.custCode + "', ";  
             query += "'"+ this.IncomingProvider.custName + "', ";  
             query += "'0', ";  
             query += "'"+ this.IncomingProvider.docDate + "',  ";  
             query += "'"+ this.inventry + "',  ";  
             query += "'13', ";  
             query += "'0', ";   //
             query += "'0', ";   //
             query += "'"+ this.invno + "', ";  
             query += "'"+ this.invamt + "', ";  
             query += "'"+ this.IncomingProvider.docNo + "', ";  
             query += "'AED', ";  
             query += "'', ";  
             query += "'', ";  
             query += "'', ";  
             query += "'', ";   
             query += "'"+ this.common.whscd  + "', ";  
             query += "'0', ";  
             query += "'0', ";  
             query += "'0', ";  
             query += "'0', ";  
             query += "'"+ this.recamt + "', ";  
             query += "'', ";  
             query += "'', ";  
             query += "'', ";  
             query += "'"+ this.common.slpcd + "', ";  
             query += "'', ";  
             query += "'', ";  
             query += "'', ";  
             query += "'', ";  
             query += "'', ";  
             query += "'"+ this.opnamt + "', ";  
             query += "'"+ this.common.userId + "', ";  
             query += "'"+ this.IncomingProvider.remark + "', ";  
             query += "'',  ";  
             query += "'"+ this.IncomingProvider.chqno + "', ";  
             query += "'"+ this.IncomingProvider.chqdt + "', ";  
             query += "'"+ this.IncomingProvider.chqdt + "',  ";   
             query += "'"+ this.IncomingProvider.chqamt + "'  ";   
             query += "  )    ";  
           
        this.database.databaseObj.executeSql(query, [])
        .then(() => {
              loading.dismiss(); // Loding Dismiss
              
              var query = ""; 
              query += "INSERT INTO TRANS1 (  docstatus, brncd, docentry ,  docnum   , objtype  , bpcode   ,  bpname   , linenum  , docdt    , baseentry, basetype ,   ";
              query += "                     baseline , visorder ,  basenum  ,  blocknum , numatcard, currency ,  itmcd    , itmnm    , barcode  , uom      , whscd    ,   ";
              query += "                     qty      , price    ,  discrt   , discamt  ,  amt      , taxcd    , taxrate  , taxsum,  slpcd    , slpnm    , addr     ,  ";
              query += "                     addr2    , mobile   ,  manage   , uniqueid  ,  userid   , remark   , freetxt,  chqno, chqdt, chqduedt, chqamt )";
              query += " VALUES   " ;
      
                   query += " ('TEMP',    "; 
                   query += "'"+ this.common.brncd + "', ";  
                   query += "'"+ this.IncomingProvider.docentry + "', ";  
                   query += "'"+ this.IncomingProvider.docNo + "', ";  
                   query += "'24',";  
                   query += "'"+ this.IncomingProvider.custCode + "', ";  
                   query += "'"+ this.IncomingProvider.custName + "', ";  
                   query += "'0', ";  
                   query += "'"+ this.IncomingProvider.docDate + "',  ";  
                   query += "'"+ this.inventry + "',  ";  
                   query += "'13', ";  
                   query += "'0', ";   //
                   query += "'0', ";   //
                   query += "'"+ this.invno + "', ";  
                   query += "'"+ this.invamt + "', ";  
                   query += "'"+ this.IncomingProvider.docNo + "', ";  
                   query += "'AED', ";  
                   query += "'', ";  
                   query += "'', ";  
                   query += "'', ";  
                   query += "'', ";   
                   query += "'"+ this.common.whscd  + "', ";  
                   query += "'0', ";  
                   query += "'0', ";  
                   query += "'0', ";  
                   query += "'0', ";  
                   query += "'"+ this.recamt + "', ";  
                   query += "'', ";  
                   query += "'', ";  
                   query += "'', ";  
                   query += "'"+ this.common.slpcd + "', ";  
                   query += "'', ";  
                   query += "'', ";  
                   query += "'', ";  
                   query += "'', ";  
                   query += "'', ";  
                   query += "'"+ this.opnamt + "', ";  
                   query += "'"+ this.common.userId + "', ";  
                   query += "'"+ this.IncomingProvider.remark + "', ";  
                   query += "'',  ";  
                   query += "'"+ this.IncomingProvider.chqno + "', ";  
                   query += "'"+ this.IncomingProvider.chqdt + "', ";  
                   query += "'"+ this.IncomingProvider.chqdt + "',  ";   
                   query += "'"+ this.IncomingProvider.chqamt + "'  ";   
                   query += "  )    ";  
                 
              this.database.databaseObj.executeSql(query, [])
              .then(() => {
              })  ; 
              this.inventry="";
              this.invno="";
              this.invdt="";
              this.invamt="";
              this.recamt=""; 
              this.opnamt=""; 
              var query = "";   
        })
        .catch(e => {
          loading.dismiss(); // Loding Dismiss
          let alert = this.alertCtrl.create({
            title: "Error",
            message: "error : "+ query + " \n" + JSON.stringify(e),
            buttons: ['OK']
          }); 
          alert.present();
        });  
    }
  }

  Next(){
     this.navCtrl.push(IncomingViewPage);
  }

}
 


