  ConnectSQLDB() {
    this.sqlite.create({
      name: this.database_name,
      location: 'default' 
    })
      .then((db: SQLiteObject) => {
        this.database.databaseObj = db; 
      })
      .catch(e => {
        alert("error : " + JSON.stringify(e))
      }); 
  }
