  string(str,len)
  {
    var item1=str
    let test = len;
    test -= item1.length;            
    for ( let i = 0; i < test; i++) {
      item1 += ' ';
    }

    return item1;
  }

  stringl(str,len)
  {
    
    var item1=str
    let test = len;
    test -= item1.length;  
    var Item2=''          
    for ( let i = 0; i < test; i++) {
      Item2 += ' ';
    }
    //alert(Item2+"1")

    //alert(Item2+item1)
    return Item2+item1

  }

