  DownloadPdf() {

    //alert("Call download.");
    const directory = this.file.dataDirectory;
    const fileName = this.printdocnumber+".pdf";
    const targetFile = directory + fileName;

    if (this.platform.is('cordova')) {
      this.pdfObj.getBuffer((buffer) => {

        // alert("File Created 0.0"+targetFile);
        var blob = new Blob([buffer], { type: 'application/pdf' });
        // alert("File Created 0.1");
        
        this.file.writeFile(directory, fileName, blob, { replace: true }).then(fileEntry => {
         
          // alert("File Created 0.2");
          this.fileOpener.open(targetFile, 'application/pdf')
          .then(() => console.log('File is opened'))
          .catch(e => alert('Error opening file' + e));

        })
      });
    } else {
      // On a browser simply use download!
      this.pdfObj.download();
      alert("PDF Downloaded 2.");
    }
  } 

  uploadOnlineData(doctype) 
  {
    let items : any 	= [];

    var query = "";
    var dno = "";
    query = " SELECT * ";
    query += " FROM TRANS   ";
    query += " WHERE docstatus='ADD' "; 
    query += " and objtype='"+ doctype +"' "; 
    query += " and userid='"+ this.userId +"' "; 
    query += " order by docentry ";
    query += "  ";
      
    this.database.databaseObj.executeSql(query, [])
    .then((res) => {  
        items = [];
        if (res.rows.length > 0) {  
          var k; 
          for(k = 0; k <= res.rows.length-1; k++)
          {
            dno = res.rows.item(k).docnum;
            items.push({ 
              Id          : res.rows.item(k).docentry    ,
              Code        : res.rows.item(k).docstatus   ,
              Name        : res.rows.item(k).docstatus   ,
              BranchCode  : res.rows.item(k).brncd       ,
              DocEntry    : res.rows.item(k).docentry    ,
              DocNum      : res.rows.item(k).docnum      ,
              DocType     : res.rows.item(k).objtype     ,
              DocDate     : res.rows.item(k).docdt       ,
              BPCode      : res.rows.item(k).bpcode      ,
              BPName      : res.rows.item(k).bpname      ,
              LineNum     : res.rows.item(k).linenum     ,
              VisOrder    : res.rows.item(k).visorder    ,
              BaseType    : res.rows.item(k).basetype    ,
              BaseEntry   : res.rows.item(k).baseentry   ,
              BaseLine    : res.rows.item(k).baseline    ,
              BaseNum     : res.rows.item(k).basenum     ,
              BlockNum    : res.rows.item(k).blocknum    ,
              NumAtCard   : res.rows.item(k).numatcard   ,
              Currency    : res.rows.item(k).currency    ,
              ItemCode    : res.rows.item(k).itmcd       ,
              ItemName    : res.rows.item(k).itmnm       ,
              Warehouse   : res.rows.item(k).whscd       ,
              Quantity    : res.rows.item(k).qty         ,
              Price       : res.rows.item(k).price       ,
              DiscRate    : res.rows.item(k).discrt      ,
              DiscAmount  : res.rows.item(k).discamt     ,
              Amount      : res.rows.item(k).amt         ,
              TaxCode     : res.rows.item(k).taxcd       ,
              TaxRate     : res.rows.item(k).taxrate     ,
              TaxSum      : res.rows.item(k).taxsum      ,
              UOM         : res.rows.item(k).uom         ,
              Barcode     : res.rows.item(k).barcode     ,
              SlpCode     : res.rows.item(k).slpcd       ,
              SlpName     : res.rows.item(k).slpnm       ,
              Address     : res.rows.item(k).addr        ,
              Address2    : res.rows.item(k).addr2       ,
              Mobile      : res.rows.item(k).mobile      ,
              Manage      : res.rows.item(k).manage      ,
              UniqueId    : res.rows.item(k).uniqueid     ,
              UserId      : res.rows.item(k).userid      ,
              Comments    : res.rows.item(k).remark      ,
              FreeTxt     : res.rows.item(k).freetxt     ,
              ChqNo       : res.rows.item(k).chqno       ,
              ChqDate     : res.rows.item(k).chqdt       ,
              ChqDueDate  : res.rows.item(k).chqduedt    ,
              ChqAmount   : res.rows.item(k).chqamt      ,
              BankCode    : res.rows.item(k).bankcd      ,
              BankName    : res.rows.item(k).banknm      ,
              ToWarehouse : res.rows.item(k).towhscd      
            });
          }
          
          let headers = new Headers();
          headers.append('Content-Type', 'application/json');
          const requestOptions = new RequestOptions({ headers:headers });
          let apiUrl=AppConstant.config.apiBaseUrl+AppConstant.config.salesinvoiceUrl+'/vansales';
          this.http.post(apiUrl, items, requestOptions)
          .subscribe((response:any) => {
            if(response.json().StatusCode==0){  
              var sapno = JSON.stringify(response);
               //this.DeleteDoc("",doctype);
               this.UpdateSAPDocNum(dno, sapno);
               alert("Data upload successfully.");         
            }
            else{
                alert("Data upload Fail.");  
            } 
          }, error => {
            alert("Something went wrong on SAP Posting !!!"); 
          });
        }   
      },
      error => { 
        alert("Something went wrong!!!"+JSON.stringify(error));
     });
  }

  UpdateSAPDocNum(docnum, SAPNum)
  {
    var query = "";
    query = " UPDATE ";
    query += "   TRANS   ";
    query += "   SET docnum='"+ SAPNum +"' , docstatus='SAP'   ";
    query += " WHERE  ";  
    query += "    docnum in ("+ docnum +") "; 

    query += "  ";
      
    this.database.databaseObj.executeSql(query, [])
    .then((res) => {  
      console.log("update successfully") 
    },
    error => { 
      alert("Something went wrong!!!"+JSON.stringify(error));
    });
  }
  DeleteDoc(docnum, doctype)
  {
    var query = "";
    query = " DELETE ";
    query += " FROM TRANS   ";
    query += " WHERE docstatus='ADD'  "; 
    query += " and objtype='"+ doctype +"' "; 
    query += " and userid='"+ this.userId +"' "; 

    if(docnum!="")
      query += "  and docnum not in ("+ docnum +") "; 

    query += "  ";
      
    this.database.databaseObj.executeSql(query, [])
    .then((res) => {  
      console.log("delete successfully") 
    },
    error => { 
      alert("Something went wrong!!!"+JSON.stringify(error));
    });
  }

