import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core'; 
import { SQLite, SQLiteObject } from '@ionic-native/sqlite';  
/*
  Generated class for the GoodsReceiptProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class DatabaseProvider {

  constructor(public http: HttpClient, public sqlite: SQLite) {
    console.log('Hello DatabaseProvider Provider');
    //this.createDB();
    //this.createTable();
    //this.GetUser();
  }

  public db : SQLiteObject;
  databaseObj: SQLiteObject;
  readonly database_name: string = "VANSALES.db";
  readonly table_name: string = "USER";
  row_data: any = [];

  createDB() {
    this.sqlite.create({
      name: this.database_name,
      location: 'default'
    })
      .then((db: SQLiteObject) => {
        this.databaseObj = db;
        //alert('Database Created!');
      })
      .catch(e => {
        alert("error " + JSON.stringify(e))
      }); 
  }

  dropTable() {
    this.databaseObj.executeSql(`DROP TABLE USER`, [])
       .then(() => {
       //alert('Table Droped!');
      }) 
      .catch(e => {
        alert("error " + JSON.stringify(e))
      });      
  }

  createTable() {
    this.databaseObj.executeSql(`CREATE TABLE IF NOT EXISTS USER (ucd varchar(20), unm varchar(100), psw varchar(50), devid varchar(100), whscd varchar(20))`, [])
      .then(() => {
       //alert('Table Created!');
      }) 
      .catch(e => {
        alert("error " + JSON.stringify(e))
      });      
  }
  
}



