import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,LoadingController,AlertController } from 'ionic-angular';
import { AppConstant } from '../../app/app.constant';
import { Http,RequestOptions,Headers } from '@angular/http';
import { GoodsReceiptProvider } from '../../providers/goods-receipt/goods-receipt'
import { CommonProvider } from '../../providers/common/common';
import { HomePage } from '../home/home';
import { SQLite, SQLiteObject } from  '@ionic-native/sqlite';   
import { DatabaseProvider } from '../../providers/database/databaseprovider';
import { IncomingProvider } from '../../providers/incoming/incoming'; 

@IonicPage()
@Component({
  selector: 'page-incoming-view',
  templateUrl: 'incoming-view.html',
})
export class IncomingViewPage {

  docNo:any="";
  userName:any="";
  itemTotalQty:any=0;
  totamt:any=0;
  
  chqamt:any=0;
  chqno:any="";
  chqdt:any="";


  salItems:any=[];
  salBatchItems:any=[];
  bluetoothList: any;
  selectedPrinter: any="";

  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    public IncomingProvider:IncomingProvider,
    public common: CommonProvider,
    public http:Http,
    public alertCtrl:AlertController,
    public GoodsReceiptProvider:GoodsReceiptProvider,
    public loadingCtrl:LoadingController,
    private sqlite: SQLite, 
    private database:DatabaseProvider) {
      this.chqno=this.IncomingProvider.chqno;
      this.chqdt=this.IncomingProvider.chqdt;
      this.chqamt=this.IncomingProvider.chqamt;
      this.getViewItems();
      //this. AllBluetoothDevices();
  }
  
  getViewItems()
  {
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start

    
    var query = "";
    query = " select baseentry,basenum, max(blocknum) blocknum, max(uniqueid) uniqueid, round(sum(amt),3) amt,  ";
    query += "    (select round(sum(amt),3) totamt from TRANS  WHERE docnum='"+ this.IncomingProvider.docNo +"'  ) totamt ";
    query += " FROM TRANS  ";
    query += "  WHERE objtype='24' and docnum='"+ this.IncomingProvider.docNo +"' ";
    query += "    ";
    query += "   group by baseentry,basenum  ";
    query += "   order by basenum  ";
    query += "    ";
    this.database.databaseObj.executeSql(query, [])
    .then((res) => {       
        loading.dismiss(); // Loding Dismiss
        this.salItems = [];
        if (res.rows.length > 0) {  
            for (var i = 0; i < res.rows.length; i++) {
              this.salItems.push(res.rows.item(i));
            }
            this.totamt=res.rows.item(0).totamt;
            //this.calculateTotalAmt();
        }  
        else
        {
          this.salItems = [];
        }
      },
      error => {
      loading.dismiss(); // Loding Dismiss
       alert("Something went wrong!!!"+JSON.stringify(error));
     }); 
  }

  saveAlert() {
  
    if( this.selectedPrinter == '')
    {
      alert("Please select Printer")
      //return
    }



    if(this.totamt==0)
    {
      let alert = this.alertCtrl.create({
        title: "Error",
        message: "Please fill all the fields",
        buttons: ['OK']
      });
      alert.present();
      return;
    }
    
    if(this.chqamt!=0)
    {
      if(this.totamt>this.chqamt)
      { 
        let alert = this.alertCtrl.create({
          title: "Error",
          message: "Total Amount must be less than or equal to Cheque Amount !!!",
          buttons: ['OK']
        });
        alert.present();
        return;
      }
    }

    let prompt = this.alertCtrl.create({
      title: 'Save Payment',
      message: "Are you sure you want to Save the document ?",
      buttons: [        
        {
          text: 'Cancel',
          handler: data => {
            console.log('Cancel');
          }
        },
        {
          text: 'Save',
          handler: data => {
            this.common.selectedPrinter1=this.selectedPrinter;
            this.Save();
          }
        }
      ]
    });
    prompt.present();
  }
  Save(){    
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
  
    var query = "";
    query = " UPDATE TRANS SET docstatus='ADD' WHERE docnum='"+ this.IncomingProvider.docNo +"' ;";
    
    this.database.databaseObj.executeSql(query, [])
    .then((res) => {
      loading.dismiss();  
      var query = "";
      query = " UPDATE TRANS1 SET docstatus='ADD' WHERE docnum='"+ this.IncomingProvider.docNo +"' ;";
      
      this.database.databaseObj.executeSql(query, [])
      .then((res) => {});
      this.common.printdocnumber=this.IncomingProvider.docNo;
      this.common.CreateIncomingPDF();
      this.common.goToHomePage();

      this.database.databaseObj.executeSql("Update USER SET nextnum="+this.common.nextnum+" where ucd='" + this.common.userId +"' ;" ,[])
      .then((res) => {
  
        this.database.databaseObj.executeSql("DELETE FROM TRANS WHERE docstatus='TEMP' ;" ,[])
        .then((res) => {})
        .catch(e => { alert("Error in Update Next Num") }); 
        
      })
      .catch(e => { alert("Error in Update Next Num") }); 


      alert("Data save successfully !!!");
    },
     error => {
      alert("Something went wrong!!!"+query+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }
  deleteAlert(docNo) {
    let prompt = this.alertCtrl.create({
      title: 'Delete Item',
      message: "Are you sure you want to Detete this item?",
      buttons: [        
        {
          text: 'Cancel',
          handler: data => {
            console.log('Cancel');
          }
        },
        {
          text: 'Delete',
          handler: data => {
            this.deleteItem(docNo);
          }
        }
      ]
    });
    prompt.present();
  }
  deleteItem(docNo,){
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    var query = "";
    query = " DELETE FROM TRANS WHERE docnum='"+ this.IncomingProvider.docNo +"' and baseentry='"+ docNo +"' ";
    
    this.database.databaseObj.executeSql(query, [])
    .then((res) => {
      var query = "";
      query = " DELETE FROM TRANS1 WHERE docnum='"+ this.IncomingProvider.docNo +"' and baseentry='"+ docNo +"' ";
      
      this.database.databaseObj.executeSql(query, [])
      .then((res) => {});
      loading.dismiss();  
      this.getViewItems();
      //this.calculateItemTotal();
      alert("Invoice delete successfully !!!");
    },
     error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }

  calculateTotalQuantity(){
    var totalquantity=0;
    for(var i in this.GoodsReceiptProvider.BatchDetails){
      for(var j in this.GoodsReceiptProvider.BatchDetails[i].BatchDetails){
        totalquantity=totalquantity+this.GoodsReceiptProvider.BatchDetails[i].BatchDetails[j].BatchQuantity*1;
      }
     
      this.GoodsReceiptProvider.BatchDetails[i].Quantity=totalquantity;
      totalquantity=0;
      this.calculateItemTotal();

    }
  }
  AllBluetoothDevices(){
    this.common.getAllBluetoothDevices();
    this.bluetoothList=this.common.bluetoothList;
    if(this.bluetoothList.length>0)
    {
    this.selectedPrinter = this.bluetoothList[0].id
    }
    }
  calculateItemTotal(){
    var totalquantity=0;
    for(var i=0;i<this.GoodsReceiptProvider.BatchDetails.length;i++){
      totalquantity=totalquantity+this.GoodsReceiptProvider.BatchDetails[i].Quantity*1;
    }
    this.itemTotalQty=totalquantity;
    totalquantity=0;
  }
  draftDocumentSave(){
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start

    this.GoodsReceiptProvider.CheckBatchQtyNull();
    this.calculateTotalQuantity();
    this.GoodsReceiptProvider.CheckItemTotalQtyNull();
    //calculating total quantity of batches
    var postUrl=AppConstant.config.apiBaseUrl+AppConstant.config.goodreceiptUrl+'/saveitems';
    let headers = new Headers();
    headers.append("Accept", '*/*');
    // headers.append('Content-Type', 'application/x-www-form-urlencoded' );
    headers.append('Content-Type', 'application/json' );
    const requestOptions = new RequestOptions({ headers:headers });
    this.http.post(postUrl,this.GoodsReceiptProvider.BatchDetails,requestOptions)
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){
        let alertPopup = this.alertCtrl.create({
          title: "Success",
          message: "Document Drafted Successfully.",
          buttons: ['OK']
        });
        alertPopup.present();
        this.navCtrl.setRoot(HomePage); 
      }
      else alert("Something went wrong");
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }

}

  


