import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

/*
  Generated class for the StockTransferProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class StockTransferProvider {

  constructor(public http: HttpClient) {
    console.log('Hello StockTransferProvider Provider');
  }
  public fromLocation:any;
  public toLocation:any;
  public docDate:any;
  public postDate:any;
  public docNo:any;
  public docentry:any;
  public lineItems:any=[];
  public fromBinList:any=[];
  public toBinList:any=[];
  public barcode:any;
  public BatchDetails:any=[];
  public fromBinName:any="";
  public fromBinCode:any="";
  public toBinCode:any="";
  public toBinName:any="";
  public itemcd:any="";
  public itemnm:any="";
  public custCode:any="";
  public custName:any="";
  public remark:any="";
  


  clearAllProperties(){
    this.fromLocation="";
    this.toLocation="";
    this.docDate="";
    this.postDate="";
    this.docNo="";
    this.lineItems=[];
    this.fromBinList=[];
    this.toBinList=[];
    this.barcode="";
  }
  CheckBatchQtyNull(){
    for(var i in this.BatchDetails){
      for(var j in this.BatchDetails[i].BatchDetails){
        if(this.BatchDetails[i].BatchDetails[j].BatchQuantity==0||this.BatchDetails[i].BatchDetails[j].BatchQuantity==null||this.BatchDetails[i].BatchDetails[j].BatchQuantity==""){
          this.BatchDetails[i].BatchDetails.splice(j,1);
        }
      }
    }
  }
  CheckItemTotalQtyNull(){
    for(var i in this.BatchDetails){
      if(this.BatchDetails[i].Quantity==0){
        this.BatchDetails.splice(i,1);
      }
    }
  }
}



