import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,LoadingController,AlertController } from 'ionic-angular';
import { AppConstant } from '../../app/app.constant';
import { CommonProvider } from '../../providers/common/common';
import {GoodsReceiptProvider} from '../../providers/goods-receipt/goods-receipt'
import { Http,RequestOptions,Headers } from '@angular/http';
import { GoodsReceiptSearchItemsPage } from '../goods-receipt-search-items/goods-receipt-search-items';
import { GoodsReceiptViewPage } from '../goods-receipt-view/goods-receipt-view';
import { GoodsReceiptViewIcPage } from '../goods-receipt-view-ic/goods-receipt-view-ic';


@IonicPage()
@Component({
  selector: 'page-goods-receipt-items-ic',
  templateUrl: 'goods-receipt-items-ic.html',
})
  export class GoodsReceiptItemsIcPage {
    fromBinCode:any="";
    barcode:any=""
    itemCode:any=""
    itemName:any=""
    binItemDetailsArray:any=[]
    // binItemDetailsArrayCopy:any=[]
    totalQty:any="";
    uom:any="";
    unitPrice:any="";
    totalCalculatedQuantity:any=0;
    constructor(public navCtrl: NavController, public navParams: NavParams,public http:Http,public alertCtrl:AlertController,public GoodsReceiptProvider:GoodsReceiptProvider,
      public loadingCtrl:LoadingController,public common: CommonProvider) {        
        this.getFromBinCode();
    }
    addBatch(line){
      var tempBatch={
        // Code:"",
        // Name:""
        BatchNum:"",//code
        BatchQuantity:"",//name
        DocNo:this.GoodsReceiptProvider.docNo,
        FromBin:this.fromBinCode.Name,
        LineId:this.fromBinCode.Code,//from bin Code  
      }
      this.binItemDetailsArray[line].BatchDetails.push(tempBatch);
    }
    deleteBatch(line,batch){
      if (batch > -1) {
        this.binItemDetailsArray[line].BatchDetails.splice(batch, 1);
      }
      this.calculateTotalQty();
    }
    calculateTotalQty(){
      this.totalCalculatedQuantity=0;
      for(var i=0;i<this.binItemDetailsArray.length;i++){
        for(var j=0;j<this.binItemDetailsArray[i].BatchDetails.length;j++){
          this.totalCalculatedQuantity+=this.binItemDetailsArray[i].BatchDetails[j].BatchQuantity*1
        }
      }
    }
    
    ionViewDidEnter(){
      this.barcode=this.GoodsReceiptProvider.barcode;
      if(this.barcode!=="")
      {
        this.searchBarcode();
      }
    }
    getFromBinCode()
    {
      let loading = this.loadingCtrl.create({
        content: 'Please wait...'
      });   
      loading.present(); // Loding Start
      this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.masterdata +'/bins?warehouse='+this.GoodsReceiptProvider.fromLocation.Code)
      .subscribe((response:any) => {
        loading.dismiss(); // Loding Dismiss
        if(response.json().StatusCode==0){
          this.GoodsReceiptProvider.fromBinList=response.json().MessageBody;
          // this.fromBinListCopy=response.json().MessageBody;
          this.fromBinCode=JSON.parse(JSON.stringify(this.GoodsReceiptProvider.fromBinList[0]));
        }
  }, error => {
    alert("Something went wrong!!!"+JSON.stringify(error));
    loading.dismiss(); // Loding Dismiss
  });
  }
  
  selectFromBin(){
    let alert = this.alertCtrl.create();
    alert.setTitle('Choose From Bin');
    for(var i=0;i<this.GoodsReceiptProvider.fromBinList.length;i++){
      alert.addInput({
        type: 'radio',
        label: this.GoodsReceiptProvider.fromBinList[i].Name,
        value: this.GoodsReceiptProvider.fromBinList[i]
      });
    }
    alert.addButton('Cancel');
    alert.addButton({
      text: 'OK',
      handler: data => {
       this.fromBinCode=JSON.parse(JSON.stringify(data));
      }
    });
    alert.present();
  }
  
  searchBarcode(){
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
  
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.purchaseorderUrl + '/icitemdetails?userName='+this.common.userId+'&itemCode=' + encodeURIComponent(this.barcode)
    +'&IcNumber='+this.GoodsReceiptProvider.refDocNo.split("_")[1])
    .subscribe((response:any) => {
      loading.dismiss(); 
      if(response.json().StatusCode==0){
        if(response.json().MessageBody==null||response.json().MessageBody==[]||JSON.stringify(response.json().MessageBody)=="[]"){
          let alertPopup = this.alertCtrl.create({
            title:"Error",
            message: "Barcode Not Found",
            buttons: ['OK']
          });
          alertPopup.present();  
          return false;
        }
        else{
          this.binItemDetailsArray=response.json().MessageBody;
          this.binItemDetailsArray.forEach(function(item){
            item.BatchDetails=[];
          });
          this.itemCode=response.json().MessageBody[0].ItemCode;
          this.itemName=response.json().MessageBody[0].ItemName;
          this.totalQty=response.json().MessageBody.map(item => item.Quantity).reduce((prev, next) => prev*1 + next*1);
          this.unitPrice=response.json().MessageBody[0].UnitPrice;
          if(response.json().MessageBody[0].Unit==""||response.json().MessageBody[0].Unit==null){
            this.uom="Mannual"
          }
          else
            this.uom=response.json().MessageBody[0].Unit;
        }
      }
      else{
        let alertPopup = this.alertCtrl.create({
          title:"Error",
          message: response.json().StatusMesssage,
          buttons: ['OK']
        });
        alertPopup.present();  
        this.barcode="";
      }
    },
     error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }
  searchItem(){
    this.navCtrl.push(GoodsReceiptSearchItemsPage);
  }
  
  Next(){
    this.navCtrl.push(GoodsReceiptViewIcPage);
  }
  
  
    addItem(){
      var existList=this.GoodsReceiptProvider.fromBinList.filter((item) => {
      return this.fromBinCode.Name==item.Name;
      });
      if(existList.length<1){
        alert("From Bin Not Found");
        return false;
      }
  
      if(JSON.stringify(this.binItemDetailsArray)=="[]"){
        let alertPopup = this.alertCtrl.create({
          title: "Error",
          message: "Batch Details not provided",
          buttons: ['OK']
        });
        alertPopup.present();  
        return false;
      }
      if(this.totalCalculatedQuantity<1){
        let alertPopup = this.alertCtrl.create({
          title: "Error",
          message: "Total Picked Quantity must be greater than 0",
          buttons: ['OK']
        });
        alertPopup.present();  
        return false;
      }
      if (
        this.GoodsReceiptProvider.BatchDetails_IC.some((item) => (
        item.FromBinCode == this.fromBinCode.Name &&  
        item.Barcode == this.barcode
        )
      )
      ) {
        let prompt = this.alertCtrl.create({
          title: 'Item Present',
          message: "Item already scanned once, save again ?",
          buttons: [        
            {
              text: 'No',
              handler: data => {
                
              }
            },
            {
              text: 'Yes',
              handler: data => {
                this.addItemPost();
              }
            }
          ]
        });
        prompt.present();
      } 
      else{
        this.addItemPost();
      }  
    }
  
    addItemPost(){
      var total=0;
      //calculate batch total
      if(this.totalCalculatedQuantity*1>this.totalQty*1){
        let alertPopup = this.alertCtrl.create({
          title: "Error",
          message: "Total batch quantity cannot exceed total line quantity",
          buttons: ['OK']
        });
        alertPopup.present();  
        return false;
      }
    //search for blank batch
    for(var i=0;i<this.binItemDetailsArray.length;i++){
      for(var j=0;j<this.binItemDetailsArray[i].BatchDetails.length;j++){
        if(this.binItemDetailsArray[i].BatchDetails[j].BatchQuantity==""||
        this.binItemDetailsArray[i].BatchDetails[j].BatchQuantity==null||
        this.binItemDetailsArray[i].BatchDetails[j].BatchQuantity==0||
        this.binItemDetailsArray[i].BatchDetails[j].BatchNum==""||
        this.binItemDetailsArray[i].BatchDetails[j].BatchNum==null||
        this.binItemDetailsArray[i].BatchDetails[j].BatchNum==0){
          this.deleteBatch(i,j);
        }
      }
    }
    for(var i=0;i<this.binItemDetailsArray.length;i++){
      this.binItemDetailsArray[i].DocNo=this.GoodsReceiptProvider.docNo;
      this.binItemDetailsArray[i].FromBinCode=this.fromBinCode.Name
      this.binItemDetailsArray[i].Barcode=this.barcode
      this.binItemDetailsArray[i].DocumentType=2
      this.binItemDetailsArray[i].FOC=this.GoodsReceiptProvider.fromLocation.Code//   from warehouse code,
      this.binItemDetailsArray[i].Packing=this.fromBinCode.Code//from bin Code
      this.binItemDetailsArray[i].Remarks=this.totalCalculatedQuantity//calculated total batch qty

    }



      let loading = this.loadingCtrl.create({
        content: 'Please wait...'
      });   
      loading.present(); // Loding Start  
      let headers = new Headers();
      headers.append("Accept", '*/*');
      // headers.append('Content-Type', 'application/x-www-form-urlencoded' );
      headers.append('Content-Type', 'application/json' );
      const requestOptions = new RequestOptions({ headers:headers });
      let apiUrl=AppConstant.config.apiBaseUrl+AppConstant.config.goodreceiptUrl+'/saveitems';
      this.http.post(apiUrl,this.binItemDetailsArray,requestOptions)
      .subscribe((response:any) => {
        if(response.json().StatusCode==0){
          loading.dismiss(); // Loding Dismiss
          console.log("saved successfully  add item");
          let alertPopup = this.alertCtrl.create({
            title: "Success",
            message: "Item Added Successfully",
            buttons: ['OK']
          });
          alertPopup.present();  
  
          //adding full item array to the provider

          for (var i=0;i<this.binItemDetailsArray.length;i++){
            this.GoodsReceiptProvider.BatchDetails_IC.push(this.binItemDetailsArray[i])
          }

          //removing all previous form data
          this.barcode="";
          this.itemCode="";
          this.itemName="";
          this.binItemDetailsArray=[];
          this.totalQty=""; 
          this.totalCalculatedQuantity=0;

        }
        else{
          alert("Something went wrong!!!"+JSON.stringify(response.json().MessageBody));
          loading.dismiss(); // Loding Dismiss

        }
      }, error => {
        alert("Something went wrong!!!"+JSON.stringify(error));
        loading.dismiss(); // Loding Dismiss
      });
    }

    validateBin(){
      let loading = this.loadingCtrl.create({
        content: 'Please wait...'
      });   
      loading.present(); // Loding Start
      this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.masterdata +'/bins?warehouse='+this.GoodsReceiptProvider.fromLocation.Code
      +'&search='+this.fromBinCode.Name)
      .subscribe((response:any) => {
        loading.dismiss(); // Loding Dismiss
        if(response.json().StatusCode==0){
         var listItem=response.json().MessageBody;
         if(listItem.length>1){
           alert("No Bin Found or Multiple Bins Found.Please go to search page")
           this.fromBinCode.Name=""
          this.fromBinCode.Code=""
         }
         else{
          this.fromBinCode.Name=listItem[0].Name
          this.fromBinCode.Code=listItem[0].Code
         }
        }
        else{
          alert("No Bin Found or Multiple Bins Found.Please go to search page")
          this.fromBinCode.Name=""
          this.fromBinCode.Code=""
        }
      }, error => {
        alert("Something went wrong!!!"+JSON.stringify(error));
        loading.dismiss(); // Loding Dismiss
      });
    }
  
  }
  


