import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,LoadingController,AlertController } from 'ionic-angular';
import { AppConstant } from '../../app/app.constant';
import { Http , RequestOptions,Headers} from '@angular/http';
import { CommonProvider } from '../../providers/common/common';
import { GoodsReceiptProvider} from '../../providers/goods-receipt/goods-receipt'
import { GoodsReceiptItemsPage } from '../goods-receipt-items/goods-receipt-items';
import { GoodsReceiptSearchVendersPage } from '../goods-receipt-search-venders/goods-receipt-search-venders';
import { GoodsReceiptItemsIcPage } from '../goods-receipt-items-ic/goods-receipt-items-ic';
import { HomePage } from '../home/home';

@IonicPage()
@Component({
  selector: 'page-goods-receipt',
  templateUrl: 'goods-receipt.html',
})
export class GoodsReceiptPage {
 
  today:any = new Date().toISOString();
  docDate:any=new Date().toISOString().split("T")[0];
  postDate:any=new Date().toISOString().split("T")[0];
  docNo:any="";
  warehouseList:any=[];
  fromLocation:any="";
  toLocation:any="";
  refDoc:any="";
  refDocNo:any="";
  VenderName:any="";
  poList:any=[];
  icList:any=[];
  poNo:any="";
  icNo:any="";
  headerRetrieveObject: any={};
  isRetrieve=false;
  constructor(public navCtrl: NavController, public navParams: NavParams,public common: CommonProvider,public GoodReceiptProvider:GoodsReceiptProvider
    ,public http:Http,public alertCtrl:AlertController,
    public loadingCtrl:LoadingController) {

    this.getLocationList();
    this.GoodReceiptProvider.venderName="";
  }
  getLastNo(){
  
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.goodreceiptUrl +'/lastno')
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){
        this.docNo=response.json().MessageBody;
        // //check radio button of locations
        this.fromLocation=this.warehouseList[0].Name;
        this.toLocation=this.warehouseList[0].Name;
        this.VenderName="";
        this.poNo="";
        this.icNo="";
        this.GoodReceiptProvider.BatchDetails=[];
      }
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }
  getLocationList(){
    var that=this;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.masterdata +'/warehouses')
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){
        this.warehouseList=response.json().MessageBody;
                // this.fromLocation=this.warehouseList[0];
      }
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }
  save()
  {
    if(this.docNo==""||this.fromLocation==""||(this.poNo==""||this.icNo=="")||this.VenderName=="")
    {
      let alert = this.alertCtrl.create({
        title: "Error",
        message: "Please fill all the fields",
        buttons: ['OK']
      });
      alert.present();
    }
    else{
      if(this.docNo<300&&this.docNo>500){
        this.navCtrl.setRoot(HomePage)
        return false
      }
      var fromLocationLocal={
        Name:this.fromLocation,
        Code:null
      }
      var docName="";
      if(this.refDoc=="PO"){
        for(var i=0;i<this.poList.length;i++){
          if(this.poList[i].DocNum==this.poNo){
            docName=this.poList[i].DocEntry;
          }
        }  
      }
      else if(this.refDoc=="IC"){
        for(var i=0;i<this.icList.length;i++){
          if(this.icList[i].IcNum==this.icNo){
            docName=this.icList[i].DocEntry;
          }
        }  
      }
        //finding code for from &to location
        for(var i=0;i<this.warehouseList.length;i++){
          if(this.warehouseList[i].Name==this.fromLocation){
            fromLocationLocal.Code=this.warehouseList[i].Code;
            break;
          }
        }
        this.GoodReceiptProvider.barcode="";
        this.GoodReceiptProvider.fromLocation=fromLocationLocal;
      this.GoodReceiptProvider.docDate=this.docDate;
      this.GoodReceiptProvider.postDate=this.postDate;
      if(this.refDoc=="PO")
        this.GoodReceiptProvider.refDocNo="PO_"+docName;
      else if(this.refDoc=="IC")
        this.GoodReceiptProvider.refDocNo="IC_"+docName;
      this.GoodReceiptProvider.docNo=this.docNo
    
      let loading = this.loadingCtrl.create({
        content: 'Please wait...'
      });   
      loading.present(); // Loding Start  
      let headers = new Headers();
      headers.append("Accept", '*/*');
      headers.append('Content-Type', 'application/x-www-form-urlencoded' );
      // headers.append('Content-Type', 'application/json' );
      const requestOptions = new RequestOptions({ headers:headers });
      if(this.refDoc=="PO"){
        var refDoc=this.poNo;
      }
      else if(this.refDoc=="IC"){
        refDoc=this.icNo
      }
      let apiUrl=AppConstant.config.apiBaseUrl+AppConstant.config.goodreceiptUrl+'/document';
      let postData="DocNo="+this.docNo+"&Vendor="+this.GoodReceiptProvider.venderCode+"&VendorName="+this.GoodReceiptProvider.venderName+
      "&Location="+this.fromLocation+"&RefDoc="+this.refDoc+"&DocDate="+this.docDate+"&PostDate="+this.postDate+"&RefDocNo="+refDoc+
      "&RefDocEntry="+docName;
      this.http.post(apiUrl,postData,requestOptions)
      .subscribe((response:any) => {
        if(response.json().StatusCode==0){
          if(this.refDoc=="PO"){
            this.navCtrl.push(GoodsReceiptItemsPage);
          }
          else if(this.refDoc=="IC"){
            this.navCtrl.push(GoodsReceiptItemsIcPage);
          }    
          loading.dismiss(); // Loding Dismiss
        }
      }, error => {
        alert("Something went wrong!!!"+JSON.stringify(error));
        loading.dismiss(); // Loding Dismiss
      });
    }
  }
  searchItem()
  {
    this.navCtrl.push(GoodsReceiptSearchVendersPage)
  }
  
  ionViewDidEnter(){

    this.VenderName=this.GoodReceiptProvider.venderName;
    if(this.VenderName!=""&&this.refDoc==""){
      this.getOpenPOList()
      this.getOpenIClist();

      // if(this.GoodReceiptProvider.refDocNo.split("_")[0]=="PO")
      // this.poNo=this.GoodReceiptProvider.refDocNo.split("_")[1]

    }
  }
  checkPOorIC(event){
    this.refDoc=event;
  }
  getOpenIClist(){
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.purchaseorderUrl +'/openiclist?userName='+this.common.userId+'&vendorCode='+this.GoodReceiptProvider.venderCode)
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){
        this.icList=response.json().MessageBody;
        if(this.isRetrieve==false)
          this.icNo=this.icList[0].IcNum;
        else  
          this.isRetrieve=false;
      }
      else{
        let alertPopup = this.alertCtrl.create({
          title: "Error",
          message: response.json().StatusMesssage,
          buttons: ['OK']
        });
        alertPopup.present(); 
      }
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }
  getOpenPOList(){
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.purchaseorderUrl +'/openpolist?userName='+this.common.userId+'&vendorCode='+this.GoodReceiptProvider.venderCode)
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){
        this.poList=response.json().MessageBody;

        if(this.isRetrieve==false)
          this.poNo=this.poList[0].DocNum;
        else  
          this.isRetrieve=false;
      }
      else{
        let alertPopup = this.alertCtrl.create({
          title: "Error",
          message: response.json().StatusMesssage,
          buttons: ['OK']
        });
        alertPopup.present(); 
      }
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }

  retrieveHeader(){
    if(this.docNo==""){
      let alertPopup = this.alertCtrl.create({
        title: "Error",
        message: "Please Enter Document No",
        buttons: ['OK']
      });
      alertPopup.present();  
    }
    else{
      let loading = this.loadingCtrl.create({
        content: 'Please wait...'
      });   
      loading.present(); // Loding Start
      this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.goodreceiptUrl +'/document/'+this.docNo)
      .subscribe((response:any) => {
        loading.dismiss(); // Loding Dismiss
        if(response.json().StatusCode==0){
          this.isRetrieve=true;
          this.headerRetrieveObject=response.json().MessageBody.DocumentHeader;
          this.GoodReceiptProvider.BatchDetails=JSON.parse(JSON.stringify(response.json().MessageBody.LineItems))
          this.docDate=this.headerRetrieveObject.DocDate;
          this.postDate=this.headerRetrieveObject.PostDate;
          this.refDocNo=this.headerRetrieveObject.RefDocNo;
          this.GoodReceiptProvider.venderCode=this.headerRetrieveObject.Vendor;
          this.GoodReceiptProvider.venderName=this.headerRetrieveObject.VendorName;
          this.VenderName=this.headerRetrieveObject.VendorName;
          this.refDoc=this.headerRetrieveObject.RefDoc;
          if(this.refDoc=="PO"){
            this.poNo=this.headerRetrieveObject.RefDocNo;
          }
          else{
            this.icNo=this.headerRetrieveObject.RefDocNo;
          }
          this.fromLocation=this.headerRetrieveObject.Location;
          this.getOpenPOList();
          this.getOpenIClist();
        }
        else{
          alert("Something went wrong");
        }
        // this.chooseBin(this.fromBinList);
      }, error => {
        alert("Something went wrong!!!"+JSON.stringify(error));
        loading.dismiss(); // Loding Dismiss
      });  
    }
  }

}
  


