import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,LoadingController,AlertController } from 'ionic-angular';
import { AppConstant } from '../../app/app.constant';
import { CommonProvider } from '../../providers/common/common';
import {GoodsReceiptProvider} from '../../providers/goods-receipt/goods-receipt'
import { Http,RequestOptions,Headers } from '@angular/http';
import { GoodsReceiptSearchItemsPage } from '../goods-receipt-search-items/goods-receipt-search-items';
import { GoodsReceiptViewPage } from '../goods-receipt-view/goods-receipt-view';

@IonicPage()
@Component({
  selector: 'page-goods-receipt-items',
  templateUrl: 'goods-receipt-items.html',
})
export class GoodsReceiptItemsPage {
  fromBinCode:any="";
  barcode:any=""
  itemCode:any=""
  itemName:any=""
  binItemDetailsArray:any=[]
  binItemDetailsArrayCopy:any=[]
  totalQty:any="";
  uom:any="";
  unitPrice:any="";
  totalCalculatedQuantity:any=0;
  constructor(public navCtrl: NavController, public navParams: NavParams,public http:Http,public alertCtrl:AlertController,public GoodsReceiptProvider:GoodsReceiptProvider,
    public loadingCtrl:LoadingController,public common: CommonProvider) {
      this.getFromBinCode();
  }

  ionViewDidEnter(){

    this.barcode=this.GoodsReceiptProvider.barcode;
    if(this.barcode!=="")
    {
      this.searchBarcode();
    }
  }
  getFromBinCode()
  {
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.masterdata +'/bins?warehouse='+this.GoodsReceiptProvider.fromLocation.Code)
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){
        this.GoodsReceiptProvider.fromBinList=response.json().MessageBody;
        // this.fromBinListCopy=response.json().MessageBody;
        this.fromBinCode=JSON.parse(JSON.stringify(this.GoodsReceiptProvider.fromBinList[0]));
      }
}, error => {
  alert("Something went wrong!!!"+JSON.stringify(error));
  loading.dismiss(); // Loding Dismiss
});
}

selectFromBin(){
  let alert = this.alertCtrl.create();
  alert.setTitle('Choose From Bin');
  for(var i=0;i<this.GoodsReceiptProvider.fromBinList.length;i++){
    alert.addInput({
      type: 'radio',
      label: this.GoodsReceiptProvider.fromBinList[i].Name,
      value: this.GoodsReceiptProvider.fromBinList[i]
    });
  }
  alert.addButton('Cancel');
  alert.addButton({
    text: 'OK',
    handler: data => {
     this.fromBinCode=JSON.parse(JSON.stringify(data));
    }
  });
  alert.present();
}

searchBarcode(){
  let loading = this.loadingCtrl.create({
    content: 'Please wait...'
  });   
  loading.present(); // Loding Start

  this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.purchaseorderUrl + '/itemdetails?userName='+this.common.userId+'&barcode=' + encodeURIComponent(this.barcode)
  +'&wareHouseCode='+this.GoodsReceiptProvider.fromLocation.Code+'&docEntry='+this.GoodsReceiptProvider.refDocNo.split("_")[1]+'&vendorcode='+this.GoodsReceiptProvider.venderCode)
  .subscribe((response:any) => {
    if(response.json().StatusCode==0){
      if(response.json().MessageBody.ItemCode==null){
        let alertPopup = this.alertCtrl.create({
          title:"Error",
          message: "Barcode Not Found",
          buttons: ['OK']
        });
        alertPopup.present();  
      }
      else{
        this.itemCode=response.json().MessageBody.ItemCode;
        this.itemName=response.json().MessageBody.ItemName;
        this.totalQty=response.json().MessageBody.Quantity;
        this.unitPrice=response.json().MessageBody.UnitPrice;
        if(response.json().MessageBody.Unit==""||response.json().MessageBody.Unit==null){
          this.uom="Mannual"
        }
        else
          this.uom=response.json().MessageBody.Unit;
      }
    }
    else{
      let alertPopup = this.alertCtrl.create({
        title:"Error",
        message: response.json().StatusMesssage,
        buttons: ['OK']
      });
      alertPopup.present();  
      this.barcode="";

    }
    loading.dismiss(); 
  },
   error => {
    alert("Something went wrong!!!"+JSON.stringify(error));
    loading.dismiss(); // Loding Dismiss
  });
}
searchItem(){
  this.navCtrl.push(GoodsReceiptSearchItemsPage);
}

Next(){
  this.navCtrl.push(GoodsReceiptViewPage);
}


  addItem(){
    var existList=this.GoodsReceiptProvider.fromBinList.filter((item) => {
    return this.fromBinCode.Name==item.Name;
    });
    if(existList.length<1){
      alert("From Bin Not Found");
      return false;
    }

    if(JSON.stringify(this.binItemDetailsArray)=="[]"){
      let alertPopup = this.alertCtrl.create({
        title: "Error",
        message: "Batch Details not provided",
        buttons: ['OK']
      });
      alertPopup.present();  
      return false;
    }
    if(this.totalCalculatedQuantity<1){
      alert("Total Picked Quantity must be greater than 0");
      return false;
    }
    if (this.GoodsReceiptProvider.BatchDetails.some((item) => (
      item.FromBinCode === this.fromBinCode.Name &&  
      item.Barcode === this.barcode
      )
    )) {
      let prompt = this.alertCtrl.create({
        title: 'Item Present',
        message: "Item already scanned once, save again ?",
        buttons: [        
          {
            text: 'No',
            handler: data => {
              
            }
          },
          {
            text: 'Yes',
            handler: data => {
              this.addItemPost();
            }
          }
        ]
      });
      prompt.present();
    } 
    else{
      this.addItemPost();
    }  
  }

  addItemPost(){
    var postObject={
      DocNo:this.GoodsReceiptProvider.docNo,
      FromBinCode:this.fromBinCode.Name,
    // ToBinCode:this.toBinCode.Name,
      Barcode:this.barcode,
      ItemCode:this.itemCode,
      ItemName:this.itemName,
      BatchDetails:[],
      Quantity:this.totalCalculatedQuantity,
      DocumentType:"1",
      Unit:this.uom,
      UnitPrice:this.unitPrice,
    // FOC:this.stockTransferProvider.fromLocation.Code+"@"+this.stockTransferProvider.toLocation.Code,//   from warehouse code
      FOC:this.GoodsReceiptProvider.fromLocation.Code,//   from warehouse code,
      Packing:this.fromBinCode.Code,//from bin Code
      Remarks:this.totalCalculatedQuantity//calculated total batch qty
    };

    //search for blank batch
    for(i=0;i<this.binItemDetailsArray.length;i++){
      if(this.binItemDetailsArray[i].Name==""||this.binItemDetailsArray[i].Code==""){
        this.deleteBatch(i);
      }
    }
    var total=0;
    //calculate batch total
    if(this.totalCalculatedQuantity>this.totalQty){
      let alertPopup = this.alertCtrl.create({
        title: "Error",
        message: "Total batch quantity cannot exceed total line quantity",
        buttons: ['OK']
      });
      alertPopup.present();  
      return false;
    }

    for(var i=0;i<this.binItemDetailsArray.length;i++){
      var BatchDetailsObject={
        BatchNum:this.binItemDetailsArray[i].Code,
        BatchQuantity:this.binItemDetailsArray[i].Name,
        DocNo:this.GoodsReceiptProvider.docNo,
        FromBin:this.fromBinCode.Name,
        //ToBin:this.toBinCode.Name,
        LineId:this.fromBinCode.Code,//from bin Code
        //LineItemId:this.toBinCode.Code//to bin Code
      }
      if(BatchDetailsObject.BatchQuantity==""||BatchDetailsObject.BatchQuantity==null||BatchDetailsObject.BatchQuantity==0)
        continue;
      postObject.BatchDetails.push(BatchDetailsObject);
    }
    console.log(JSON.stringify(postObject));
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start  
    let headers = new Headers();
    headers.append("Accept", '*/*');
    // headers.append('Content-Type', 'application/x-www-form-urlencoded' );
    headers.append('Content-Type', 'application/json' );
    const requestOptions = new RequestOptions({ headers:headers });
    let apiUrl=AppConstant.config.apiBaseUrl+AppConstant.config.goodreceiptUrl+'/saveitem';
    this.http.post(apiUrl,(postObject),requestOptions)
    .subscribe((response:any) => {
      if(response.json().StatusCode==0){
        loading.dismiss(); // Loding Dismiss
        console.log("saved successfully  add item");
        let alertPopup = this.alertCtrl.create({
          title: "Success",
          message: "Item Added Successfully",
          buttons: ['OK']
        });
        alertPopup.present();  

        //adding full item array to the provider
        this.GoodsReceiptProvider.BatchDetails.push(postObject);

        //removing all previous form data
        this.barcode="";
        this.itemCode="";
        this.itemName="";
        this.binItemDetailsArray=[];
        this.binItemDetailsArrayCopy=[];
        this.totalQty=""; 
        this.totalCalculatedQuantity=0;
      }
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }
  addBatch(){
    var tempBatch={
      Code:"",
      Name:""
    }
    this.binItemDetailsArray.push(tempBatch);
  }
  deleteBatch(index){
    if (index > -1) {
      this.binItemDetailsArray.splice(index, 1);
    }
    this.calculateTotalQty();
  }
  calculateTotalQty(){
    this.totalCalculatedQuantity=0;
    for(var i=0;i<this.binItemDetailsArray.length;i++){
      this.totalCalculatedQuantity=this.totalCalculatedQuantity+this.binItemDetailsArray[i].Name*1;
    }
  }

}



