import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,LoadingController,AlertController } from 'ionic-angular';
import { AppConstant } from '../../app/app.constant';
import { Http,RequestOptions,Headers } from '@angular/http'; 
import { CommonProvider } from '../../providers/common/common';
import { HomePage } from '../home/home';
import { SQLite, SQLiteObject } from  '@ionic-native/sqlite';   
import { DatabaseProvider } from '../../providers/database/databaseprovider'; 
import { StockTransferProvider } from '../../providers/stock-transfer/stock-transfer';

/**
 * Generated class for the StockTransferViewPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-stock-transfer-view',
  templateUrl: 'stock-transfer-view.html',
})
export class StockTransferViewPage {

  docNo:any="";
  userName:any="";
  totalQuantity:any=0;
  
  salItems:any=[];
  salBatchItems:any=[];

  constructor(public navCtrl: NavController, 
    public navParams: NavParams,  public http:Http,
    public alertCtrl:AlertController,
    public StockTransferProvider:StockTransferProvider,
    public loadingCtrl:LoadingController,
    public common: CommonProvider,
    private sqlite: SQLite, 
    private database:DatabaseProvider) {
      this.docNo=this.StockTransferProvider.docNo;
      this.userName=this.common.userId;
      this.getViewItems();
  }
  
  ionViewDidLoad() {
    console.log('ionViewDidLoad StockTransferViewPage');
  }

  getViewItems()
  {
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start

    
    var query = "";
    query = " select itmcd,itmnm,round(sum(qty),3) qty, round(sum(amt),3) amt, round(sum(taxsum),3) taxsum, round(max(price),3) price,  ";
    query += "    (select round(sum(qty),3) totqty from TRANS  WHERE docnum='"+ this.StockTransferProvider.docNo +"'  ) totqty ";
    query += " FROM TRANS  ";
    query += "  WHERE docnum='"+ this.StockTransferProvider.docNo +"' ";
    query += "    ";
    query += "   group by itmcd,itmnm ";
    query += "    ";
    this.database.databaseObj.executeSql(query, [])
    .then((res) => {       
        loading.dismiss(); // Loding Dismiss
        this.salItems = [];
        if (res.rows.length > 0) {  
            for (var i = 0; i < res.rows.length; i++) {
              this.salItems.push(res.rows.item(i));
            }
            this.totalQuantity=res.rows.item(0).totqty;
        }  
        else
        {
          this.salItems = [];
        }
      },
      error => {
      loading.dismiss(); // Loding Dismiss
       alert("Something went wrong!!!"+JSON.stringify(error));
     }); 
  }

  Save(){
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
  
    var query = "";
    query = " UPDATE TRANS SET docstatus='ADD' WHERE docnum='"+ this.StockTransferProvider.docNo +"' ;";
    
    this.database.databaseObj.executeSql(query, [])
    .then((res) => {
      //this.common.uploadOnlineData('13');
      loading.dismiss();  
      this.common.printdocnumber=this.docNo;
      this.common.CreateStockPDF();
      this.common.goToHomePage();
      var query1 = "";
      query1 = " UPDATE TRANS1 SET docstatus='ADD' WHERE docnum='"+ this.StockTransferProvider.docNo +"' ;";
      this.database.databaseObj.executeSql(query1, [])
    .then((res) => {
    }, error => {});

    this.database.databaseObj.executeSql("Update USER SET nextnum="+this.common.nextnum+" where ucd='" + this.common.userId +"' ;" ,[])
    .then((res) => {

      this.database.databaseObj.executeSql("DELETE FROM TRANS WHERE docstatus='TEMP' ;" ,[])
      .then((res) => {})
      .catch(e => { alert("Error in Update Next Num") }); 
      
    })
    .catch(e => { alert("Error in Update Next Num") });  

      alert("Data save successfully !!!");
    },
     error => {
      alert("Something went wrong!!!"+query+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
   
  }
  deleteItem(docNo,itemId){
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    var query = "";
    query = " DELETE FROM TRANS WHERE docnum='"+ this.StockTransferProvider.docNo +"' and itmcd='"+ docNo +"' ";
    
    this.database.databaseObj.executeSql(query, [])
    .then((res) => {
      loading.dismiss();  
      this.getViewItems();
      //this.calculateItemTotal();
      var query1 = "";
      query1 = " DELETE FROM TRANS1 WHERE docnum='"+ this.StockTransferProvider.docNo +"' and itmcd='"+ docNo +"' ";
    
    this.database.databaseObj.executeSql(query1, [])
    .then((res) => {});
      //this.calculateItemTotal();
      alert("Item delete successfully !!!");
    },
     error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }
  saveAlert() {
    let prompt = this.alertCtrl.create({
      title: 'Save Invoice',
      message: "Are you sure you want to Save the document ?",
      buttons: [        
        {
          text: 'Cancel',
          handler: data => {
            console.log('Cancel');
          }
        },
        {
          text: 'Save',
          handler: data => {
            this.Save();
          }
        }
      ]
    });
    prompt.present();
  }
  deleteAlert(docNo,itemId) {
    let prompt = this.alertCtrl.create({
      title: 'Delete Item',
      message: "Are you sure you want to Detete this item?",
      buttons: [        
        {
          text: 'Cancel',
          handler: data => {
            console.log('Cancel');
          }
        },
        {
          text: 'Delete',
          handler: data => {
            this.deleteItem(docNo,itemId);
          }
        }
      ]
    });
    prompt.present();
  }

}



