import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,LoadingController,AlertController } from 'ionic-angular';
import { Http } from '@angular/http';
import { AppConstant } from '../../app/app.constant';
 import { StockTransferProvider } from '../../providers/stock-transfer/stock-transfer';

@IonicPage()
@Component({
  selector: 'page-stock-transfer-to-bin-search',
  templateUrl: 'stock-transfer-to-bin-search.html',
})
export class StockTransferToBinSearchPage {

  listItem:any=[]
  pageIndex:any
  codeOrName:any=""


  constructor(public navCtrl: NavController, public navParams: NavParams, public http:Http,public alertCtrl:AlertController,
    public loadingCtrl:LoadingController,public StockTransferProvider:StockTransferProvider) {

  }
  getList()
  {
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.masterdata +'/bins?warehouse='+this.StockTransferProvider.toLocation.Code+'&search='+this.codeOrName)
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){
       this.listItem=response.json().MessageBody;
      }
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }
  returnBin(name,code)
  {
    this.StockTransferProvider.toBinCode=code;
    this.StockTransferProvider.toBinName=name;
    this.navCtrl.pop();
  }

}



