import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,LoadingController,AlertController } from 'ionic-angular';
import { Http } from '@angular/http';
import { AppConstant } from '../../app/app.constant';
//import { PickListProvider } from '../../providers/pick-list/pick-list';
import { CommonProvider } from '../../providers/common/common'; 
import { SQLite, SQLiteObject } from  '@ionic-native/sqlite';  
import { DatabaseProvider } from '../../providers/database/databaseprovider';
import { CustomerSearchPage } from '../customer-search/customer-search'; 
import { SalesInvoiceProvider} from '../../providers/sales-invoice/sales-invoice';

/**
 * Generated class for the RouteVisitEntryPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-route-visit-entry',
  templateUrl: 'route-visit-entry.html',
})
export class RouteVisitEntryPage {

  pageIndex:any;
  ItemCode:any="";
  ItemName:any="";
  codeOrName:any="";
  batchNum:any="";
  listItem:any=[];
  
  docEntry:any=0;
  docnum:any="";
  docDate:any=new Date().toISOString().split("T")[0];
  baseType:any="";
  baseNum:any="";
  custcode:any="";
  custName:any="";
  retremark:any="";
  remark:any="";

  constructor(public navCtrl: NavController, public navParams: NavParams, 
    public http:Http,public alertCtrl:AlertController,
    public loadingCtrl:LoadingController,
    public common: CommonProvider,
    private sqlite: SQLite, 
    public SalesInvoiceProvider:SalesInvoiceProvider,
    private database:DatabaseProvider) {
      this.pageIndex=1; 
      this.common.getnextnum(); 
  }

  ionViewDidEnter() {
    this.custName=this.SalesInvoiceProvider.custName;
    this.custcode=this.SalesInvoiceProvider.custCode;
  }

  CreateNew()
  {
    if(this.docnum=="")
    {
    //this.common.getnextnum();
   // this.common.getnextnum();
    this.docEntry= this.common.nextnum;
    this.docnum= this.common.newdocnum; 
    }
  }
   
  searchItem()
  {
    this.navCtrl.push(CustomerSearchPage);
  }

  Save(){
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
  
    var query = "";
  
    var query = ""; 
    query += "INSERT INTO TRANS (  docstatus, brncd, docentry ,  docnum   , objtype  , bpcode   ,  bpname   , linenum  , docdt    , baseentry, basetype ,   ";
    query += "                     baseline , visorder ,  basenum  ,  blocknum , numatcard, currency ,  itmcd    , itmnm    , barcode  , uom      , whscd    ,   ";
    query += "                     qty      , price    ,  discrt   , discamt  ,  amt      , taxcd    , taxrate  , taxsum,  slpcd    , slpnm    , addr     ,  ";
    query += "                     addr2    , mobile   ,  manage   , uniqueid  ,  userid   , remark   , freetxt )";
    query += " VALUES    " ;
 
    query += " ('ADD',    "; 
    query += "'"+ this.common.brncd + "', ";  
    query += "'"+ this.docEntry + "',     ";  
    query += "'"+ this.docnum + "',       ";  
    query += "'VISIT', ";  
    query += "'"+ this.custcode + "',     ";  
    query += "'"+ this.custName + "',     ";  
    query += "'0', ";  
    query += "'"+ this.docDate +"',       ";  
    query += "'',  ";  
    query += "'',  ";  
    query += "'',  ";   
    query += "'',  ";   
    query += "'',  ";  
    query += "'',  ";  
    query += "'"+ this.docnum + "',       ";  
    query += "'',  ";  
    query += "'',  ";  
    query += "'',  ";  
    query += "'',  ";  
    query += "'',  ";   
    query += "'"+ this.common.whscd  + "',";  
    query += "'',  ";  
    query += "'0', ";  
    query += "'0', ";  
    query += "'0', ";  
    query += "'0', ";  
    query += "'',  ";  
    query += "'',  ";  
    query += "'',  ";  
    query += "'"+ this.common.slpcd + "', ";  
    query += "'',  ";  
    query += "'',  ";  
    query += "'',  ";  
    query += "'',  ";  
    query += "'',  ";  
    query += "'',  ";  
    query += "'"+ this.common.userId + "', ";  
    query += "'"+ this.remark +"',         ";  
    query += "'"+ this.retremark +"'         ";  
    query += ")    ";   

    this.database.databaseObj.executeSql(query, [])
    .then((res) => {
      loading.dismiss();  
      this.common.goToHomePage();

      this.database.databaseObj.executeSql("Update USER SET nextnum="+this.common.nextnum+" where ucd='" + this.common.userId +"' ;" ,[])
      .then((res) => {
  
        this.database.databaseObj.executeSql("DELETE FROM TRANS WHERE docstatus='TEMP' ;" ,[])
        .then((res) => {})
        .catch(e => { alert("Error in Update Next Num") }); 
        
      })
      .catch(e => { alert("Error in Update Next Num") }); 


      alert("Data save successfully !!!");
    },
    error => {
      alert("Something went wrong!!!"+query+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  } 
  saveAlert() {
    if(this.docnum==""||this.custName==""||this.custcode==""||this.remark=="")
    {
      let alert = this.alertCtrl.create({
        title: "Error",
        message: "Please fill all the fields",
        buttons: ['OK']
      });
      alert.present();
    }
    else
    {        
      let prompt = this.alertCtrl.create({
        title: 'Save Visit',
        message: "Are you sure you want to Save the Visit Entry ?",
        buttons: [        
          {
            text: 'Cancel',
            handler: data => {
              console.log('Cancel');
            }
          },
          {
            text: 'Save',
            handler: data => { 
              this.Save();
            }
          }
        ]
      });
      prompt.present();
    }
  }
}



