import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,LoadingController,AlertController } from 'ionic-angular';
import { AppConstant } from '../../app/app.constant';
import { Http , RequestOptions,Headers} from '@angular/http';
import { CommonProvider } from '../../providers/common/common';
import { SalesInvoiceProvider} from '../../providers/sales-invoice/sales-invoice';
import { SalesInvoiceItemsPage } from '../salesinvoice-items/salesinvoice-items';  
import { HomePage } from '../home/home'; 
import { SQLite, SQLiteObject } from  '@ionic-native/sqlite';   
import { DatabaseProvider } from '../../providers/database/databaseprovider';
import { CustomerSearchPage } from '../customer-search/customer-search'; 
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { empty } from 'rxjs/Observer';

@IonicPage()
@Component({
  selector: 'page-salesinvoice',
  templateUrl: 'salesinvoice.html',
})
export class SalesInvoicePage {
 
  
  docEntry:any=0;
  docnum:any="";
  docDate:any=new Date().toISOString().split("T")[0];
  baseType:any="";
  baseNum:any="";
  custName:any="";
  remark:any="";
  

  soNo:any="";
  icNo:any="";
  soData: any = [];
  icData: any = [];  

  
  

  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    public common: CommonProvider,
    public SalesInvoiceProvider:SalesInvoiceProvider,
    public http:Http,
    public alertCtrl:AlertController,
    public loadingCtrl:LoadingController,
    private sqlite: SQLite, 
    private database:DatabaseProvider 
    ) {
      
    this.SalesInvoiceProvider.custName="";
    this.baseType="NA";
    this.common.getnextnum();
  }

  searchItem()
  {
    this.navCtrl.push(CustomerSearchPage);
  }

  offlineSave()
  {  
    if(this.docnum=="" || this.custName==""|| this.docnum == null)
    {
      let alert = this.alertCtrl.create({
        title: "Error",
        message: "Please fill all the fields",
        buttons: ['OK']
      });
      alert.present();
    }
    else
    {  
      if(this.baseType!="NA"&&this.soNo==""&&this.icNo=="")
      {
          let alert = this.alertCtrl.create({
            title: "Error",
            message: "Please fill all the fields no",
            buttons: ['OK']
          });
        alert.present();
      }
      else
      {           
          this.SalesInvoiceProvider.docentry=this.docEntry;
          this.SalesInvoiceProvider.docNo=this.docnum;
          this.SalesInvoiceProvider.docDate=this.docDate;
          this.SalesInvoiceProvider.refDocNo=this.baseNum;
          this.SalesInvoiceProvider.refDocType=this.baseType;
          this.SalesInvoiceProvider.remark=this.remark;
          this.navCtrl.push(SalesInvoiceItemsPage);
      }
    }
  }
 
  CreateNew()
  {
    if(this.docnum=="")
    {
    //this.common.getnextnum();
    this.newn();
    }
   
  }
  newn()
  {
    //this.common.getnextnum();
    this.docEntry= this.common.nextnum;
    this.docnum= this.common.newdocnum;
  }

  ionViewDidEnter(){
    if(this.custName!=this.SalesInvoiceProvider.custName)
    {
        this.baseType="NA"; 
        this.soNo="";
        this.icNo=""; 
    }
    
    this.custName=this.SalesInvoiceProvider.custName;
    if(this.SalesInvoiceProvider.custCode!=""){ 
      this.getOpenDocs();
      this.getOpenBlock();
    }
  }

  checkSOorIC(event){
    this.baseType=event;
    if(this.baseType!="NA")
      this.SalesInvoiceProvider.basetype="17";
    else
      this.SalesInvoiceProvider.basetype="";
  }

  getBaseDetail(event,type){
    var qry=""; 
    if(type=="SO")
      qry="SELECT * FROM TRANS WHERE objtype='17' and docnum='"+event+"'";
    else
      qry="SELECT * FROM TRANS WHERE objtype='17' and blocknum='"+event+"'";
    
      this.database.databaseObj.executeSql(qry, [])
    .then((res) => { 
      if (res.rows.length > 0) { 
        if(res.rows.length>0)  
        {
          this.SalesInvoiceProvider.baseentry=res.rows.item[0].docentry;
          this.SalesInvoiceProvider.basenum=res.rows.item[0].docnum;
          this.SalesInvoiceProvider.baseblocknum=res.rows.item[0].blocknum;
        }
      }  
    })
    .catch(e => {
      alert("Invalid Base Doc")
    });
  }

  getOpenDocs()
  {
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present();  
    var query = "";
    query = "SELECT DISTINCT docnum FROM TRANS WHERE bpcode='" + this.SalesInvoiceProvider.custCode +"' order by docnum ; ";
    this.database.databaseObj.executeSql(query, [])
    .then((res) => {
      loading.dismiss();  
      this.soData = [];
      if (res.rows.length > 0) { 
        for (var i = 0; i < res.rows.length; i++) {
            this.soData.push(res.rows.item(i));
          }
      }  
    })
    .catch(e => {
      loading.dismiss(); 
      alert("Invalid Base Doc")
    });
  }

  getOpenBlock()
  {
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present();  
    
    var query = "";
    query = "SELECT DISTINCT blocknum FROM TRANS WHERE bpcode='" + this.SalesInvoiceProvider.custCode +"' order by blocknum ; ";
    this.database.databaseObj.executeSql(query, [])
    .then((res) => {
      loading.dismiss();  
        this.icData = [];
        if (res.rows.length > 0) { 
          for (var i = 0; i < res.rows.length; i++) {
              this.icData.push(res.rows.item(i));
            }
        }  
    })
    .catch(e => {
      loading.dismiss(); 
      alert("Invalid Base Doc")
    });
  }
}
  


